<template>
  <div :class="[{'fontsS':cn},{'fonte':en}]">
    <Introduce></Introduce>
    <Sguecotope></Sguecotope>
    <Threeyayers></Threeyayers>
    <Martixecto></Martixecto>
    <Newadvance></Newadvance>
    <Gamecenter></Gamecenter>
  </div>
</template>

<script>
import Introduce from "./HomePages/Introduce";
import Sguecotope from "./HomePages/Sguecotope";
import Threeyayers from "./HomePages/Threeyayers";
import Martixecto from "./HomePages/Martixecto";
import Newadvance from "./HomePages/Newadvance";
import Gamecenter from "./HomePages/Gamecenter";
export default {
  data() {
    return { cn: true, en: false };
  },
  mounted() {
    this.en = localStorage.getItem("locale") == "en" ? true : false;
    this.cn = localStorage.getItem("locale") == "zh" ? true : false;
  },
  beforeUpdate() {
    this.en = localStorage.getItem("locale") == "en" ? true : false;
    this.cn = localStorage.getItem("locale") == "zh" ? true : false;
  },
  components: {
    Introduce,
    Sguecotope,
    Threeyayers,
    Martixecto,
    Newadvance,
    Gamecenter
  }
};
</script>

<style lang="less" >
.fontsS {
  font-family: Source Han Sans CN;
}
.fonte {
  font-family: pha;
}
</style>