</template>

<script>
export default {
  mounted() {
    var lang = localStorage.getItem("locale");

    this.cn = lang == "zh" ? true : false;
    this.en = lang == "en" ? true : false;
  },
  beforeUpdate() {
    var lang = localStorage.getItem("locale");
